/* Scroll animations */

.scroll {
  opacity: 0;
}

.scrolled.fade-in {
  -webkit-animation: fade-in 1s ease-in-out both;
          animation: fade-in 1s ease-in-out both;
}

.scrolled.fade-in-bottom {
  -webkit-animation: fade-in-bottom 1s ease-in-out both;
          animation: fade-in-bottom 1s ease-in-out both;
}

.scrolled.slide-left {
  -webkit-animation: slide-in-left 1s ease-in-out both;
          animation: slide-in-left 1s ease-in-out both;
}

.scrolled.slide-right {
  -webkit-animation: slide-in-right 1s ease-in-out both;
          animation: slide-in-right 1s ease-in-out both;
}

@-webkit-keyframes slide-in-left {
  0% {
    -webkit-transform: translate3d(-100px,0,0);
            transform: translate3d(-100px,0,0);
    opacity: 0;
  }
  100% {
    -webkit-transform: translate3d(0,0,0);
            transform: translate3d(0,0,0);
    opacity: 1;
  }
}

@keyframes slide-in-left {
  0% {
    -webkit-transform: translate3d(-100px,0,0);
            transform: translate3d(-100px,0,0);
    opacity: 0;
  }
  100% {
    -webkit-transform: translate3d(0,0,0);
            transform: translate3d(0,0,0);
    opacity: 1;
  }
}

@-webkit-keyframes slide-in-right {
  0% {
    -webkit-transform: translate3d(100px,0,0);
            transform: translate3d(100px,0,0);
    opacity: 0;
  }
  100% {
    -webkit-transform: translate3d(0,0,0);
            transform: translate3d(0,0,0);
    opacity: 1;
  }
}

@keyframes slide-in-right {
  0% {
    -webkit-transform: translate3d(100px,0,0);
            transform: translate3d(100px,0,0);
    opacity: 0;
  }
  100% {
    -webkit-transform: translate3d(0,0,0);
            transform: translate3d(0,0,0);
    opacity: 1;
  }
}

@-webkit-keyframes fade-in-bottom {
  0% {
    -webkit-transform: translate3d(0,50px,0);
            transform: translate3d(0,50px,0);
    opacity: 0;
  }
  100% {
    -webkit-transform: translate3d(0,0,0);
            transform: translate3d(0,0,0);
    opacity: 1;
  }
}

@keyframes fade-in-bottom {
  0% {
    -webkit-transform: translate3d(0,50px,0);
            transform: translate3d(0,50px,0);
    opacity: 0;
  }
  100% {
    -webkit-transform: translate3d(0,0,0);
            transform: translate3d(0,0,0);
    opacity: 1;
  }
}

@-webkit-keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.home {
    position: relative;
    overflow-x: hidden;
    height: 125vh;
}

.home-background {
    height: 100%;
    background-image: url(/static/media/introDesert.c818a659.png);
    background-size: cover;
    background-position: top center;
}

.home-base {
    height: 120vh;
    color: white;
}

.home-parallaxBack {
    -webkit-transform: translateZ(-4px) scale(5.25);
            transform: translateZ(-4px) scale(5.25);
    background: #b1dbf5;
    justify-content: flex-start;
    padding-left: 10%;
}

.home-sun {
    height: 20rem;
    width: 20rem;
    background: yellow;
    border-radius: 50%;
    box-shadow: 0 0 15px 15px yellow;
}

.home-parallaxMid {
    -webkit-transform: translateZ(-1px) scale(2.1);
            transform: translateZ(-1px) scale(2.1);
}

.home-parallaxFront {
    -webkit-transform: translateZ(0);
            transform: translateZ(0);
}
.sand {
    position: absolute;
    height: 20%;
    bottom: 0;
    left: 0;
    background: #cb7f07;
}

.sand-hill {
    height: 15rem;
    width: 15rem;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    background: #cb7f07;
}

.home-banner {
    height: 110%;
    font-size: 175%;
    text-shadow: 5px 5px 3px rgba(0,0,0,0.33);
    background: linear-gradient(to bottom, transparent, rgba(0,0,0,0.33));
    /*background-image: url('./images/banner3.png');
    background-size: contain;
    background-repeat: no-repeat;*/
}

.home-banner > h1 {
    padding: 0 15rem;
}

#cloud1 {
    position: absolute;
    top: 40%;
    right: -8rem;
    height: 5rem;
    width: 8rem;
    opacity: 0.75;
    -webkit-animation: clouds 30s 1s linear infinite;
            animation: clouds 30s 1s linear infinite;
}

#cloud2 {
    position: absolute;
    top: 35%;
    right: -8rem;
    height: 5rem;
    width: 8rem;
    opacity: 0.65;
    -webkit-animation: clouds 45s 2s linear infinite;
            animation: clouds 45s 2s linear infinite;
}

#cloud3 {
    position: absolute;
    top: 10%;
    right: -12rem;
    height: 7rem;
    width: 12rem;
    -webkit-animation: clouds 35s -10s linear infinite;
            animation: clouds 35s -10s linear infinite;
}

@-webkit-keyframes clouds {
    0% {
        -webkit-transform: translateX(0);
                transform: translateX(0)
    }
    100% {
        -webkit-transform: translateX(-110vw);
                transform: translateX(-110vw)
    }
}

@keyframes clouds {
    0% {
        -webkit-transform: translateX(0);
                transform: translateX(0)
    }
    100% {
        -webkit-transform: translateX(-110vw);
                transform: translateX(-110vw)
    }
}

.header {
    background: rgba(255,255,255,0.75) !important;
    position: absolute;
    top: 0;
    left: 0;
    flex-direction: column;
    z-index: 1;
}

.header-top {
    height: 10vh;
    justify-content: space-between;
    padding: 0 1rem;
}

.header-top > svg {
    cursor: pointer;
}

.header-links {
    top: 100%;
    flex-direction: column;
    max-height: 0;
    overflow: hidden;
    z-index: 300;
    transition: max-height 0.25s;
}
.header-link {
    justify-content: flex-start;
    width: 100%;
    height: 3rem;
    padding-left: 2rem;
    border-bottom: 1px solid rgba(0,0,0,0.1);
    cursor: pointer;
}

.header-link:hover > p {
    opacity: 0.5;
}
.header-link > p {
    font-size: 0.75rem;
    margin: 0.5rem;
}

.socials {
    width: 33.33%;
